import * as React from "react"

const containerStyle = {
    width: "100%",
    display: "flex",
    flexShrink: 1,
    justifyContent: "center"
}
const imageStyle = {
    maxHeight: "70vh",
    maxWidth: "100%"
}
export default function CustomImage(props) {
    return (
        <div style={containerStyle}>
            <img src={props.src} alt={props.alt} style={imageStyle}/>
        </div>
    )
}
