import * as React from "react"
import ContentSwitcher from "../components/content_switcher"

const parentStyle = {
    display: "flex",
    width: "100%",
    justifyContent: "space-between"
}
const parentStyleWide = {
    flexDirection: "row",
    gap: "20px"
}
const parentStyleNarrow = {
    flexDirection: "column",
    gap: "10px",
    justifyContent: "center",
}
const childrenStyle = {
    flexShrink: 1
}

export default function Group(props) {
    return (
        <div style={{
            ...parentStyle, 
            ...props.narrowWindow ? parentStyleNarrow : parentStyleWide}}>
        {
            typeof props.content !== 'undefined' ?
            props.content.map (item => {
                return (
                    <div style={childrenStyle}>
                        { ContentSwitcher(item) }
                    </div>
                )
            })
            :
            props.children
        }
        </div>
    )
}
