import * as React from "react"

const containerStyle = {
  display: "flex",
  flexGrow: 1,
  alignItems: "center",
  gap: 50
}
const titleStyle = {
  fontFamily: "Roboto",
  fontWeight: 400
}
const imageStyle = {
  width: "60%",
  border: 0
}

export default function TextImage(props) {
  return (
    <div style={{
      ...containerStyle,
      ...{margin: props.narrowWindow ? "0px 0px 0px 0px" : "-90px -105px -100px 0px"}}}>
      <h1 style={{
        ...titleStyle,
        ...{fontSize: props.narrowWindow ? 20: 25}
      }}>{props.title}</h1>
      { 
        props.narrowWindow ? <div/> : 
          <img src={props.src} alt={props.alt} style={imageStyle}/>
      }
    </div>
  )
}
