import * as React from "react"

const parentStyle = {
    display: "flex",
    flexDirection: "column",
    width: "100%"
}
const titleStyle = {
    fontFamily: "Poppins",
    fontSize: 14,
    fontWeight: 600,
    textTransform: "uppercase",
    letterSpacing: "0.24em"
}
const listStyle = {
    paddingLeft: 18,
    margin: "0px 0px 0px 0px"
}
const itemStyle = {
    fontFamily: "Roboto",
    fontSize: 18,
    fontWeight: 400,
    marginTop: 0
}

export default function TextList(props) {
    return (
        <div style={parentStyle}>
        <h1 style={titleStyle}>{props.title}</h1>
        {/* <ul style={listStyle}>
            {props.items.map((item, index) => {
                return <li style={itemStyle}>{item}</li>
            })}
        </ul> */}
        <p style={itemStyle}>{props.items.join(", ")}</p>
        </div>
    )
}
