import * as React from "react"
import { Link } from "gatsby"
const titleStyle = {
  fontFamily: "Poppins",
  fontSize: 14,
  fontWeight: 600,
  textTransform: "uppercase",
  letterSpacing: "0.24em"
}
const bodyStyle = {
  fontFamily: "Roboto",
  fontSize: 18,
  fontWeight: 400,
  marginTop: 0,
  whiteSpace: "pre-line"
}
const linkStyle = {
  textDecoration: "none",
  color: "#4F89D6",
  fontWeight: "bold"
}

export default function TextBody(props) {
  return (
    <div>
      <h1 style={titleStyle}>{props.title}</h1>
      <p style={bodyStyle}>{props.body.split(" ").map(item => {
        let link = props.links.find(x => x.keyword === item)
        if (link !== undefined) {
          return <Link 
                    to={link.address} 
                    style={linkStyle}
                    target="_blank"
                  >{item + " "}</Link>
        }
        else return item + " "
      })}</p>
    </div>
  )
}

TextBody.defaultProps = {
  links: []
}
