import * as React from "react"
import LeftBox from "../components/left_box"
import ContentSwitcher from "../components/content_switcher"

const parentStyle = {
    display: "flex",
    flexDirection: "row",
    alignItems: "center",
    width: "100%",
    borderBottomStyle: "solid",
    borderWidth: 1
}
const contentContainer = {
    display: "flex",
    flexDirection: "column",
    gap: 25,
    width: "100%",
    height: "100%"
}
const contentContainerWide = {
    padding: "90px 105px 100px 105px"
}
const contentContainerNarrow = {
    padding: "20px 20px 20px 20px"
}
const titleStyle = {
    fontFamily: "Poppins",
    fontWeight: 600,
    letterSpacing: "0.24em",
    fontSize: 14,
    textTransform: "uppercase",
    textAlign: "right",
    transform: "rotate(180deg)",
    writingMode: "vertical-lr"
}
const leftBoxStyle = {
    justifyContent: "right"
}

export default function ContentSection(props) {
    return (
        <main style={parentStyle}>
            <LeftBox 
            narrowWindow={props.narrowWindow}
            style={{
                ...leftBoxStyle,
                ...{paddingTop: props.narrowWindow? "30px":"100px"}
            }}>
                <h1 style={titleStyle}>{props.title}</h1>
            </LeftBox>
            <div style={{
                ...contentContainer, 
                ...props.narrowWindow ? contentContainerNarrow : contentContainerWide}}>
            {
                typeof props.content !== "undefined" ?
                props.content.map (item => {
                    return ContentSwitcher(item, props.narrowWindow) 
                }) 
                :
                props.children
            }
            </div>
        </main>
    )
}
